<template>
    <SpaceWar>
        <!-- <div class="card">
          <div class="card-body">
              hhh
          </div> -->




        <!-- 模态框 -->
        <div class="modal" id="myModal">
            <div class="modal-dialog">
                <div class="modal-content">

                    <!-- 模态框头部 -->
                    <div class="modal-header">
                        <!-- <h4 class="modal-title">模态框标题</h4> -->
                        <p class="text-primary" style="font-size:larger;font-weight: 900;">发布学习</p>

                        <button type="button" class="btn-close" data-bs-dismiss="modal"></button>
                    </div>

                    <!-- 模态框内容 -->
                    <div class="modal-body">
                        <label class="form-label">学习时长：{{ timeLength }}分钟</label>
                        <input v-model="timeLength" type="range" class="form-range" min="1" max="180" id="customRange2">
                        <span class="form-labe">开始时间:&nbsp;</span>
                        <span class="text-info ">{{ now_time }}</span>
                        <br>

                        <span class="form-labe">结束时间:&nbsp;</span>
                        <span class="text-info ">{{ end_time }}</span>
                        <hr>
                        <label for="titleInput" class="form-label">计划：</label>
                        <input type="text" v-model="tittle" class="form-control" id="titleInput"
                            placeholder="学习科目/知识点...">
                        <br>
                        <label for="titleInput" class="form-label">详细内容：</label>
                        <textarea v-model="content" class="form-control" id="Textarea" placeholder="描述学习..."
                            rows="4"></textarea>


                    </div>

                    <!-- 模态框底部 -->
                    <div class="modal-footer">

                        <button @click="set_post" class="btn btn-info ">开始</button>
                        <button type="button" class="btn btn-danger" data-bs-dismiss="modal">关闭</button>
                        <label class="text-danger">{{ error_message }}</label>
                        <!-- <button @click="gpt" class="btn btn-info ">chat</button> -->



                    </div>

                </div>
            </div>
        </div>





        <!-- <div class="card opacity-75" style="margin-top: 3%;">
            <div class="card-body"> -->
        <div style="margin-top: 3%;">
            <table class="table table-borderless">

                <thead>
                    <tr>
                        <th scope="col">
                            <p class="text-warning " style="font-weight: 600;">计划
                                <br>

                                <button v-if="$store.state.user.is_login" class="btn btn-primary btn-sm"
                                    data-bs-toggle="modal" data-bs-target="#myModal"
                                    style="float: left;">开始我的学习</button>

                            </p>
                        </th>

                    </tr>
                </thead>
                <tbody>
                    <tr v-for="post in posts" :key="post.post_id">
                        <th scope="row">
                            <div class="card opacity-100">
                                <div class="card-body">


                                    <!-- <p class=""> -->
                                    <span class=""><kbd>计划</kbd></span>
                                    <span :class="'small ' + post.Tcolor">{{ ' ' + post.tittle }} </span>
                                    <span
                                        v-if="new Date().getTime() < new Date(post.write_time).getTime() + post.time_length * 60 * 1000"
                                        class="small text-warning" style="float: right;"> 剩余
                                        {{ Math.floor((new
                            Date(post.write_time).getTime()
                            + post.time_length * 60 * 1000 - new Date().getTime()) / 60000) }}
                                        分钟</span>

                                    <span v-else class="small text-primary" style="float: right;">已完成</span>
                                    <!-- </p> -->
                                    <br>
                                    <!-- <p class=""> -->
                                    <span class=""><kbd>内容</kbd></span>
                                    <span :class="'small ' + post.Tcolor">{{ ' ' + post.content }}</span>


                                    <!-- </p> -->
                                    <!-- <p class=""> -->
                                    <br>
                                    <span class=""><kbd>开始时间</kbd></span>

                                    <span :class="'small ' + post.Tcolor">{{ ' ' + new
                            Date(post.write_time).toLocaleString() }}
                                    </span>
                                    <br>
                                    <span class=""><kbd>时长</kbd></span>
                                    <span :class="'small ' + post.Tcolor">{{ ' ' + post.time_length }}分钟
                                        &nbsp;&nbsp;</span>

                                    <br>
                                    <!-- </p>
                                    <p> -->
                                    <span class=""><kbd>来自</kbd></span>
                                    <span @click="get_friend_info(post.user_id)" :class="'small ' + post.Tcolor">{{
                            ' ' + post.username }}</span>

                                    <button @click="delete_post"
                                        v-if="post.username == $store.state.user.username && new Date().getTime() < new Date(post.write_time).getTime() + post.time_length * 60 * 1000"
                                        class="btn btn-info btn-sm" style="float:right">取消计划</button>

                                    <div class="progress"
                                        v-if="new Date().getTime() < new Date(post.write_time).getTime() + post.time_length * 60 * 1000">
                                        <div class="progress-bar  progress-bar-striped progress-bar-animated"
                                            role="progressbar"
                                            :style="'width: ' + parseInt((new Date().getTime() - new Date(post.write_time).getTime()) / 60 / 10 / post.time_length) + '%;'"
                                            aria-valuenow="25" aria-valuemin="0" aria-valuemax="100">
                                            {{ parseInt((new Date().getTime() - new Date(post.write_time).getTime()) /
                            60 /
                            10 / post.time_length) }}%</div>
                                    </div>
                                    <!-- <button class="btn btn-secondary btn-sm" style="float:right">评论</button> -->

                                    <!-- </p> -->

                                </div>
                            </div>
                        </th>
                    </tr>


                    <div class="card opacity-50">
                        <div class="card-body">
                            <span style="font-size: small;" v-if="hitokoto != null && hitokoto.data!= null && hitokoto.data.content != null &&hitokoto != ''">
                                <p>{{ hitokoto.data.content }}</p>
                                        <p style="float: right;">{{ "——"+hitokoto.data.author }}</p>
                            </span>


                        </div>
                    </div>

                </tbody>



            </table>
            <nav aria-label="Page">
                <ul class="pagination" style="float:right">
                    <li class="page-item" @click="click_page(-1)"><a class="page-link" href="#">上一页</a></li>
                    <li @click="click_page(page.number)" :class="'page-item ' + page.is_active"
                        v-for=" page in showPage" :key="page.number">
                        <a class="page-link" href="#">{{ page.number }}</a>
                    </li>

                    <li class="page-item" @click="click_page(0)"><a class="page-link" href="#">下一页</a></li>
                </ul>
            </nav>


        </div>

        <!-- </div> -->






    </SpaceWar>
</template>
<script>

import SpaceWar from "@/components/post/SpaceWar.vue";
import { ref } from "vue";
import { useStore } from 'vuex'
import $ from 'jquery'
// import router from "@/router/index"
export default {
    name: "PhoneStudyView",
    components: {
        SpaceWar: SpaceWar,
    },




    setup() {
        new Date();
        const store = useStore();
        let content = ref('');
        const posts = ref([]);
        const PAGE = 6;


        let currentPage = 1;
        let totalPage = 1;
        let showPage = ref([]);
        let timeLength = ref();
        timeLength.value = 30
        let now_time = ref("");
        let end_time = ref("");



        let tittle = ref("");
        let error_message = ref("");


        // now_time.value = new Date().toLocaleTimeString();
        // end_time.value = new Date(new Date().getTime() + timeLength.value * 60 * 1000).toLocaleTimeString();

        setInterval(() => {
            now_time.value = new Date().toLocaleTimeString();
            end_time.value = new Date(new Date().getTime() + timeLength.value * 60 * 1000).toLocaleTimeString();
        }, 1000)

        const set_post = () => {
            error_message.value = ""
            if (!store.state.user.is_login) {
                error_message.value = "只有作怪才能开始学习"
                return
            }

            if (tittle.value.length < 1 || content.value.length < 1) {
                error_message.value = "不能为空"
                return 
            }

            $.ajax({
                url: store.state.BaseUrl + "/api/post/",
                type: "post",
                headers: {
                    Authorization: "Bearer " + store.state.user.token
                },
                data: {

                    user_id: store.state.user.user_id,
                    tittle: tittle.value,
                    time_length: timeLength.value,
                    write_time: new Date(),
                    content: content.value,
                },
                success(resp) {
                    

                    if (resp.code != 1)
                        error_message.value = resp.content.message
                    else {
                        error_message.value = ""
                        tittle.value = ""
                        content.value = ""
                    }

                    pull_page(1)
                },
                error(resp) {
                    console.log("post发布失败")

                    error_message.value = resp.message
                }
            })
        }

        const delete_post = () => {
            $.ajax({
                url: store.state.BaseUrl + "/api/post/",
                type: "delete",
                headers: {
                    Authorization: "Bearer " + store.state.user.token
                },
                data: {
                    user_id: store.state.user.user_id,
                },
                success(resp) {
                    if (resp.code != 1) { console.log("失败") }
                    else {
                        pull_page(1)
                    }
                },
                error(resp) {
                    console.log("失败")

                    error_message.value = resp.message
                }
            })
        }







        const pull_page = page => {
            if(page > 3 && !store.state.user.is_login){
                alert("不给看了")
                return
            }



            currentPage = page;
            $.ajax({
                url: store.state.BaseUrl + "/api/post/common/",
                type: "get",
                data: {
                    page: currentPage
                },
                success(resp) {
                    if (resp.code == 1) {
                        resp = resp.content
                        posts.value = resp.items;


                        console.log(resp);
                        totalPage = resp.page_count;
                        update_pages();
                    } else {
                        console.log(resp.message)
                    }
                },
                error(resp) {
                    console.log(resp.message);
                    console.log("拿页面失败")
                }
            })
        }
        pull_page(1);

        const update_pages = () => {
            let max_pages = parseInt(Math.ceil(totalPage / PAGE));
            let new_page = [];
            for (let i = currentPage - 2; i < currentPage + 2; i++) {
                if (i >= 1 && i <= max_pages) {
                    new_page.push({
                        number: i,
                        is_active: i === currentPage ? "active" : ""
                    })
                }
            }
            showPage.value = new_page;
        }

        const click_page = page => {
            if (page === -1) {
                page = currentPage - 1;
            } else if (page === 0) {
                page = currentPage + 1;
            }
            let max_pages = parseInt(Math.ceil(totalPage / PAGE))
            if (page >= 1 && page <= max_pages) {
                pull_page(page);
            }
        }




        // const gpt=()=>{
        //   $.ajax({
        //   url:"https://v2.api-m.com/api/chatgpt",
        //   type:"get",

        //   <div class="spinner-border text-info"></div>
        //   data: {
        //     msg: "写一个科幻小说大纲"
        //   }
        //   ,
        //   success(resp){
        //     console.log(resp);
        //     content.value = resp.data;
        //   },
        //   error(resp){
        //     console.log(resp);
        //   }
        // })
        // }

        // const get_friend_info = user_id => {
        //     store.dispatch("get_friend", {
        //         user_id: user_id,
        //         success() {
        //             console.log(store.state.friend)
        //             router.push({ name: "friend" })
        //         }, error(resp) {
        //             console.log(resp)
        //         }
        //     })
        // }


        let hitokoto = ref("");
        const get_str = () => {
            hitokoto.value = '';
            $.ajax({
                type: 'get',
                url: "https://v2.alapi.cn/api/shici",
                data: {
                    type: "all",
                    token: store.state.music.token,
                    
                    
                },
                success(resp) {
                    hitokoto.value = resp;
                    // console.log(resp)
                }
            })


        }
        get_str();
        return {
            hitokoto,
            click_page,
            // gpt,
            posts,
            showPage,
            content,
            timeLength,
            now_time,
            end_time,
            set_post,
            delete_post,
            tittle,
            error_message,
            // get_friend_info



            // text_color
        }
    }
}

</script>
<style scoped>
td {
    font-size: small;
    font-weight: 600;
    font-family: 黑体;
}

span {
    font-size: small;
    font-weight: 600;

    font-family: 微软雅黑;


}

th {
    font-size: x-large;
    font-weight: 1000;

    font-family: 微软雅黑;
}
</style>